import React, { Component } from 'react';
import { connect } from "react-redux";

import Header from "./HeaderContainer";
import DetailHeader from "../components/detail/header"
import Comment from "../components/comment"
import api from "../utils/api"
import '../styles/board.scss';
import backImg from '../images/global/btn-back@2x.png';

class BoardDetailContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      content: {
        images: [],
        created_at: "",
        content: "",
        total_comments: 0,
        comments: []
      },
      comment: "",
      board_id: window.location.getParameterByName('id'),
    };
    this.changeHandler = this.changeHandler.bind(this);
    this.submitComment = this.submitComment.bind(this);
    this.getApi = this.getApi.bind(this);
    this.goBack = this.goBack.bind(this);
  }

  componentDidMount() {
    this.getApi();
  }

  async getApi() {
    const {board_id} = this.state;
    try {
      const response = await api('get',`/api/v1/community/${board_id}`);
      if(response.status < 300) {
        this.setState({
          content: response.data.data,
          comment: "",
        });
      }
    } catch(e) {
      alert('게시글 정보를 가오는데 실패했습니다\n잠시 후 다시 시도해주세요')
    }
  }

  changeHandler(e) {
    this.setState({[e.target.name]: e.target.value});
  }

  async submitComment() {
    const {comment, board_id} = this.state;
    const postData = {
      content: comment
    }
    const response = await api('post', `/api/v1/community/${board_id}/comments`, postData);
    if(response.status === 201) {
      swal('댓글이 등록되었습니다');
      this.getApi();
    } else {
      swal('댓글 등록에 실패했습니다');
    }
  }

  goBack(){
    const {history} = this.props;
    history.push('/board');
  }

  render() {
    const {content, comment} = this.state;
    return (
      <div>
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">게시글</div>
        </div>
        <div className="board-container child-container">
          <div className="detail-container">
            <DetailHeader board={content} />
            {
              content.images.map((item, index) => 
                <img key={index} src={item.image} />
              )
            }
            <div className="detail-content">{content.content}</div>
            <div className="comment-container">
              <div className="title">
                <span>댓글 </span>
                {content.comments.length}
              </div>
              <div className="comment-body">
                <table>
                  <colgroup>
                    <col width='56' />
                    <col width='auto' />
                  </colgroup>
                  <tbody>
                    {
                      content.comments.map((item, index) =>
                        <Comment key={index} comment={item} />
                      )
                    }
                  </tbody>
                </table>
              </div>
            </div>
            <div className="comment-inputbox">
              <input className="comment-input" type="text" placeholder="댓글달기" name="comment" onChange={this.changeHandler} value={comment} />
              <button className="register-button" type="button" onClick={this.submitComment}>전송</button>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default BoardDetailContainer;

